.skills_content {
    display: flex;
    flex-direction: column;
    background-color: #232323;
    justify-content: center;
    align-items: center;
}

.skills_content h1 {
   color: #f76a52;
   font-size: var(--divheadings);

}
.skills_content h3 {
        color: #ffffff;
        font-size: 42px;
        margin-top: 5rem;
        margin-bottom: 5rem;
    }

.skills_progress {
    flex: 1;
    display:grid;
    grid-template-columns: repeat(2, 1fr);
    grid-column-gap: 10rem;
}

.skill_icons {
    display: flex;
    margin-top: 5rem;
    justify-content: space-between;
    align-items: center;
    gap: 3rem;
}

.skill_icons a:hover{
    cursor: pointer;
    opacity: 80%;
    transform: scale(1.2);
    transition: 0.1s ease;    

}
